@import url('https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@100;200;300;400;500;600;700;800;900&display=swap');

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    font-family: 'Be Vietnam Pro', sans-serif;
    overflow-x: hidden;
}

/* style.css */
.zoom-fade-animation {
    animation: zoomFadeOut;
    animation-duration: 1s; /* Animáció időtartama */
    animation-timing-function: ease-in-out; /* Animáció időzítése (lelassulás) */
    animation-fill-mode: forwards; /* Az animáció befejezése után az utolsó állapotot tartja meg */
    animation-delay: 0.7s; /* Késleltetés az animáció elindulása előtt (500ms) */ /* Kezdeti átlátszatlanság */
    transform: scale(1.3); /* Kezdeti méretnagyítás */
  }
  
  @keyframes zoomFadeOut {
    0% {
        transform: scale(1.3); /* Normál méret a kezdeti állapotban */
    }
    50% {
        transform: scale(1.3);
    }
    100% {
      transform: scale(1); /* A végső méret a nagyításhoz */
    }
  }

.navbar {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 2;
}    

.navitems {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.navitems li {
    list-style: none;
}

.navitems li a{
    text-decoration: none;
    color: #FFFFFA;
    font-size: 20px;
    font-weight: 400;
}

.reservbtn{
    width: 300px;
    height: 100px;
    background-color: #080708;
    padding: 15px 50px;
    border-radius: 37px;
    color: #FFFFFA;
}

.landing-container {
    width: 100%;
    height: 100vh;
    background-image: url(../public/landingbg.png);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 50px;
}

.text {
    display: flex;
    flex-direction: column;
    gap: 0px;
}

.text p {
    width: 650px;
    color: #FFFFFA;
    font-size: 32px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    padding-left: 20px;
    opacity: 0;
}

.landing-container h1 {
    color: rgba(0, 0, 0, 0.17);
    font-size: 350px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    -webkit-text-stroke-width: 7px;
    -webkit-text-stroke-color: #FFFFFA;
    margin: 0;
    line-height: 0.8;
}

.tracking-in-expand{animation-delay: 2s; animation:tracking-in-expand .7s cubic-bezier(.215,.61,.355,1.000) both}
  @keyframes tracking-in-expand {
    0% {
        letter-spacing:-.5em;
        opacity:0
    }
    40% 
    {
        opacity:.6
    
    }
    
    100% {
        opacity:1
    }
    }

    .text-focus-in{animation-delay: 2s; animation:text-focus-in 1s cubic-bezier(.55,.085,.68,.53) both}
    @keyframes text-focus-in{0%{filter:blur(12px);opacity:0}100%{filter:blur(0);opacity:1}}

    .slide-right{animation:slide-right .5s cubic-bezier(.25,.46,.45,.94) both}
    @keyframes slide-right{0%{transform:translateX(-600px)}100%{transform:translateX(0px)}}

    .slide-top{animation: slide-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s both;}
    @keyframes slide-top {
        0% {
            opacity: 0;
            transform: translateY(100px);
        }
        100% {
            opacity: 1;
            transform: translateY(0px);
        }
      }
      
.about-us {
    width: 100%;
    height: 50vh;
    background-color: #FFFFFA;
}      

.wrapper {
    display: flex;
    align-items: first baseline;
    width: 100%;
    justify-content: space-between;
    padding: 0px 70px;
    box-sizing: border-box;
}

.leftwrapper p{
    color: #121212;
    font-size: 32px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: -1.44px;
}

.rightwrapper {
    width: 650px;
}

.rightwrapper h2 {
    color: #080708;
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.8px;
    opacity: 0.8;
}

.mobile-navbar {
    display: none;
    position: absolute;
    z-index: 2;
    width: 100%;
}

.hamburger {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
    width: 100%;
    height: 100px;
    padding: 30px;
    box-sizing: border-box;
    position: absolute;
    z-index: 3;
    justify-content: center;
    cursor: pointer;
}

.hamline {
    height: 1px;
    width: 50px;
    background-color: #FFFFFA;
}

.hamline:nth-child(2) {
    width: 30px;
}

.hamline:nth-child(3) {
    width: 40px;
}

.mobilenavitems {
    background-color: #FFFFFA;
    height: 700px;
    position: absolute;
    width: 100%;
    z-index: 2;
    display: none;
}

.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mobilenavitems ul{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    padding-left: 0;
}

.mobilenavitems li {
    list-style: none;
}

.mobilenavitems li a {
    text-decoration: none;
    color: #080708;
}

.mobilereservbtn {
    width: 300px;
    height: 100px;
    background-color: #080708;
    padding: 15px 50px;
    border-radius: 37px;
    color: #FFFFFA;
}

.slide-bottom{animation:slide-bottom .5s cubic-bezier(.25,.46,.45,.94) both}
@keyframes slide-bottom{0%{transform:translateY(-600px)}100%{transform:translateY(0px)}}

.menuactive span:nth-child(1) {
    transform: rotate(-46deg);
    background-color: var(--bg);
    transition: all 0.35s ease-in;
}

.menuactive span:nth-child(2) {
    opacity: 0;
    transition: all 0.05s ease-in;
}

.menuactive span:nth-child(3) {
    transform: rotate(46deg);
    position: relative;
    top: -15px;
    transition: all 0.35s ease-in;
}

@media only screen and (max-width: 600px) {
    .navbar {
        display: none;
    }

    .mobile-navbar {
        display: block;
    }
}